//var globalVar=require('./core/global-variable.js');//常用标志变量
require("./core/main-nav.js");
var Konva=null;
var drawImg=require('./core/draw-img.js');
try{
    Konva=require("konva");
    Konva.pixelRatio = 1;//针对高分屏的性能优化
}catch(err){};
fillPage();
var canvasStage,canvasLayer,canvasAnim,animItem,canvasOld,canvasNew;//canvas相关参数
var handleTimer=null;//节流定时器
$(window).on('resize',function(){
    clearTimeout(handleTimer);
    handleTimer=setTimeout(function(){
        globalVar.winW=window.innerWidth ||document.documentElement.clientWidth || document.body.clientWidth;
        isDrawCavas();
        if(canvasOld!=canvasNew){
            if(canvasNew=='pc'){//如果是PC
                drawCanvas();
            }
            if(canvasNew=='mobile'){//如果是小屏或者移动
                drawMobileCanvas();
            }
        }
    },300);
});
//内容响应
function fillPage(){
    var funVideoO=$('#funVideo');
    var funGameClubO=$("#funGameClub");
    var gameClubBannerImgO=$("#gameClubBannerImg");
    if(!isDrawCavas()){//不支持canvas的就直接用图片替代位置
        replaceHtml(gameClubBannerImgO);
    }else{
        if(canvasNew=='pc'){//如果是PC
            drawCanvas();
        }
        if(canvasNew=='mobile'){//如果是小屏或者移动
            drawMobileCanvas();
        }
    }
    //如果是移动端或者浏览器不支持视频就执行视频dom插入
    if(globalVar.mobile || !globalVar.video || globalVar.winW<780){
        replaceHtml(funGameClubO);
    }else if(globalVar.video && globalVar.winW>780){
        replaceHtml(funVideoO);
    }
};
//判断是走移动，还是走PCcanvas;
function isDrawCavas(){
    if(globalVar.canvas){
        if((globalVar.mobile && globalVar.winW<=780) || globalVar.winW<=780){
            //drawMobileCanvas();
            canvasNew='mobile';
        }else{
            //drawCanvas();
            canvasNew='pc';
        }
        return true;
    }
    return false;

}
