/*
 回调函数,也是4个icon的运动的主逻辑
 @param obj是当前传入的图片或精灵对象
 @param cx是当前应该围绕旋转的中心点x坐标
 @param cy是当前应该围绕旋转的中心点y坐标
 @param cr是当前应该围绕旋转的半径值
 @param ox是相对定位的另一点x坐标
 @param oy是相对定位的另一点y坐标
 @param difx是x方向的位置差
 @param dify是y方向的位置差
 */
function circleMove(obj,cx,cy,cr,ox,oy,difx,dify,lineo){
    var angleAdd=0;
    var angleSubtract=0;
    var circleO={
        circleObj:obj,
        cx:cx,
        cy:cy,
        cr:cr,
        ox:ox,
        oy:oy,
        difx:difx,
        dify:dify,
        line:lineo,
        bz:Math.random()
    };
    animItem.push(circleO);
    if(animItem.length==4){//只有4个icon全部加载完成才可启动动画
        canvasAnim = new Konva.Animation(function(frame) {
            angleAdd++;
            angleSubtract--;
            if(angleAdd>360){
                angleAdd=0;
            };
            if(angleSubtract<-360){
                angleSubtract=0;
            }
            for(var i=0,len=animItem.length;i<len;i++){
                rotateAn(animItem[i],animItem[i].bz>0.5 ? angleAdd : angleSubtract,aniBack);
            }
        }, canvasLayer);
        canvasAnim.start();
    }
}
//动画完回调
function aniBack(obj,x,y){
    var lineo=obj.line;
    lineo.setAttrs({'points':[obj.ox,obj.oy,x,y]});
    //lineo.moveToBottom();
    //lineo.draw();
}
//元素坐标转换实现
function rotateAn(obj,angle,fn){
    var radius=obj.cr;
    var midX=obj.cx;
    var midY=obj.cy;
    var iconX,iconY;
    var rotateO=obj.circleObj ? obj.circleObj : null;
    if(angle>=0){
        if(angle<90 && angle>0){
            iconX=midX+Math.cos(angle/180*Math.PI)*radius;
            iconY=midY+Math.sin(angle/180*Math.PI)*radius;
        }else if(angle==90){
            iconX=midX;
            iconY=midY+radius;
        }else if(angle>90 && angle<180){
            iconX=midX-Math.sin((angle-90)/180*Math.PI)*radius;
            iconY=midY+Math.cos((angle-90)/180*Math.PI)*radius;
        }else if(angle==180){
            iconX=midX-radius;
            iconY=midY;
        }else if(angle>180 && angle<270){
            iconX=midX-Math.cos((angle-180)/180*Math.PI)*radius;
            iconY=midY-Math.sin((angle-180)/180*Math.PI)*radius;
        }else if(angle==270){
            iconX=midX;
            iconY=midY-radius;
        }else if(angle>270 && angle<360){
            iconX=midX+Math.sin((angle-270)/180*Math.PI)*radius;
            iconY=midY-Math.cos((angle-270)/180*Math.PI)*radius;
        }else if(angle==360 || angle==0){
            iconX=midX+radius;
            iconY=midY;
        };
    }else{
        if(angle>-90 && angle<0){
            iconX=midX+Math.cos(Math.abs(angle)/180*Math.PI)*radius;
            iconY=midY-Math.sin(Math.abs(angle)/180*Math.PI)*radius;
        }else if(angle==-90){
            iconX=midX;
            iconY=midY-radius;
        }else if(angle>-180 && angle<-90){
            iconX=midX-Math.sin(Math.abs(angle+90)/180*Math.PI)*radius;
            iconY=midY-Math.cos(Math.abs(angle+90)/180*Math.PI)*radius;
        }else if(angle==-180){
            iconX=midX-radius;
            iconY=midY;
        }else if(angle>-270 && angle<-180){
            iconX=midX-Math.cos(Math.abs(angle+180)/180*Math.PI)*radius;
            iconY=midY+Math.sin(Math.abs(angle+180)/180*Math.PI)*radius;
        }else if(angle==-270){
            iconX=midX;
            iconY=midY+radius;
        }else if(angle>-360 && angle<-270){
            iconX=midX+Math.sin(Math.abs(angle+270)/180*Math.PI)*radius;
            iconY=midY+Math.cos(Math.abs(angle+270)/180*Math.PI)*radius;
        }else if(angle==-360){
            iconX=midX+radius;
            iconY=midY;
        };
    }
    if(rotateO){
        rotateO.setX(parseInt(iconX+obj.difx));
        rotateO.setY(parseInt(iconY+obj.dify));
    };
    fn && fn(obj,iconX,iconY);
    return {
        x:iconX,
        y:iconY
    };
};
//执行html替换
function replaceHtml(obj){
    obj.replaceWith(obj.attr('data-html'));
}
//stage销毁
function destoryItem(){
    if(canvasStage){//销毁元素
        canvasStage.destroyChildren();
        canvasStage.destroy();
    }
    if(canvasAnim){//暂停动画
        canvasAnim.stop();
    }
}


